header {
  height: 60px;
  width: 100%;
  background: white;
  display: flex; }
  @media (max-width: 767px) {
    header {
      height: 300px; } }
  header .header-container {
    width: 1024px;
    margin: auto;
    display: flex; }
    @media (max-width: 767px) {
      header .header-container {
        flex-direction: column;
        margin: 0.5em; } }
    header .header-container .header-logo {
      flex: 1;
      margin: auto;
      font-size: 1.2em;
      font-family: "Noto Sans KR", sans-serif;
      font-weight: 500;
      color: #333; }
      @media (max-width: 767px) {
        header .header-container .header-logo {
          flex: 1;
          margin: auto;
          padding: 0.5em 0; } }
    header .header-container .header-menu {
      flex: 4;
      margin: auto;
      display: flex;
      justify-content: flex-end; }
      @media (max-width: 767px) {
        header .header-container .header-menu {
          flex: 10;
          flex-direction: column;
          width: 90%; } }
      header .header-container .header-menu * {
        margin: auto 0.5em; }
