.cardList-container {
  padding: 1em;
  width: 1024px;
  margin: auto;
  display: flex;
  flex-direction: column; }
  @media (max-width: 767px) {
    .cardList-container {
      width: 90%; } }
  .cardList-container .cardList-list {
    display: flex;
    flex-wrap: wrap; }
    @media (max-width: 767px) {
      .cardList-container .cardList-list {
        flex-direction: column;
        width: 100%; } }
    .cardList-container .cardList-list div {
      width: 240px;
      display: flex;
      flex-direction: column;
      margin: 0.5em; }
      @media (max-width: 767px) {
        .cardList-container .cardList-list div {
          width: 100%;
          margin: 0.5em auto; } }
